import React from 'react';
import styled from '@emotion/styled';
import { useHistory, Link } from 'react-router-dom';
import { mutate } from 'swr';

import { SongListPage } from 'app/RouteSwitch';
import { songsAPI } from 'features/songs/songsAPI';
import { useTransition } from '../transition/TransitionContext';
import { ProgressBar } from '../transition/ProgressBar';

export function Header() {
  const [transition, setTransition] = useTransition();
  const history = useHistory();

  const handleTransition = async (e: React.MouseEvent<HTMLAnchorElement>) => {
    e.preventDefault();
    setTransition(true);
    await Promise.all([
      SongListPage.preload(),
      mutate('songList', songsAPI.fetchAll(), false),
    ]);
    setTransition(false);
    history.push('/');
  };

  return (
    <Container>
      <ProgressBar active={transition ? true : false} />
      <HomeLink to="/" onClick={handleTransition}>
        Cantopop
      </HomeLink>
    </Container>
  );
}

const Container = styled.div`
  font-size: 16px;
  background: #0e0e0e;
  color: #d9d9de;
  padding: 20px 40px;
`;

const HomeLink = styled(Link)`
  color: #d9d9de;
  text-decoration: none;
  cursor: pointer;
`;
