import React, { Suspense } from 'react';
import { Route, Switch } from 'react-router-dom';

import { lazy } from 'app/lazy';
import { SongListSkeleton } from 'features/songs/list/SongListSkeleton';
import { SongDetailsSkeleton } from 'features/songs/details/SongDetailsSkeleton';

export const SongListPage = lazy(
  () => import('features/songs/list/SongListPage')
);
export const SongDetailPage = lazy(
  () => import('features/songs/details/SongDetailsPage')
);

export function RouteSwitch() {
  return (
    <Switch>
      <Route exact path="/">
        <Suspense fallback={<SongListSkeleton />}>
          <SongListPage />
        </Suspense>
      </Route>
      <Route exact path="/songs/:songId">
        <Suspense fallback={<SongDetailsSkeleton />}>
          <SongDetailPage />
        </Suspense>
      </Route>
    </Switch>
  );
}
