function timeout(ms: number) {
  return new Promise((resolve) => setTimeout(resolve, ms));
}

export const songsAPI = {
  async fetchAll() {
    await timeout(1500);
    const res = await fetch(`${process.env.REACT_APP_API_BASE_URL}/songs/`);
    const json = await res.json();
    return json;
  },
  async fetchById(songId: string) {
    await timeout(1500);
    const res = await fetch(
      `${process.env.REACT_APP_API_BASE_URL}/songs/${songId}/`
    );
    const json = await res.json();
    return {
      id: json.id,
      artist: json.artist,
      title: json.title,
      youtubeId: json.youtube_id,
      lyrics: json.lyrics.map((rawLyrics: any) => ({
        provider: rawLyrics.provider,
        locale: rawLyrics.locale,
        geniusSongId: rawLyrics.genius_song_id,
        startIndex: rawLyrics.start_index,
        endIndex: rawLyrics.end_index,
      })),
    };
  },
};
